  get length() {
    return Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
  }

  set length(value) {
    let len = this.x * this.x + this.y * this.y + this.z * this.z;

    if (len === 0) {
      this.x = value;
      this.y = this.z = 0;
    } else {
      len = value / Math.sqrt(len);
      this.x *= len;
      this.y *= len;
      this.z *= len;
    }
  }

